import React, { useState } from 'react';
import photo from '../../utils/assets/photos/alfaiataria.png';

import './styles.scss';

function getWindowDimensions() {
  const { innerWidth: width, innerHeight: height } = window;

  return {
    width,
    height
  };
}

export function Tailoring() {
  const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());

  return (
    <div id="container-tailoring">

      {
        windowDimensions.width < 991 &&
        <div className="photo">
          <img src={photo} alt="photo-alfaiataria" />
        </div>
      }
      <div className="content">
        <div className="header">
          <h1>ALFAIATARIA</h1>
          <p>TOTALMENTE FEITO A MO SOB MEDIDA</p>
        </div>

        <div>
          <ul className="list">
            <div>
              <li>Fio Super 120 e 130</li>
              <li>L fria Australiana</li>
              <li>Botes Importados</li>
            </div>
            <div>
              <li>Ternos e Calas</li>
              <li>Palets e Smokings</li>
              <li>Sociais e Esportivos</li>
            </div>
          </ul>
        </div>
        <button>Quero um oramento</button>
      </div>

      {
        windowDimensions.width > 991 &&
        <div className="photo">
          <img src={photo} alt="photo-alfaiataria" />
        </div>
      }

    </div>
  );
}
